<template>
  <v-container fluid grid-list-md>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <v-card>
          <v-img src="https://cdn.vuetifyjs.com/images/lists/ali.png" height="300px">
            <v-layout column fill-height>
              <v-card-title>
                <router-link to="/">
                  <v-btn dark icon>
                    <v-icon>chevron_left</v-icon>
                  </v-btn>
                </router-link>

                <v-spacer></v-spacer>

                <v-btn dark icon class="mr-3" 
                  :to="{name: 'edit-employee', params: {employeeId: employeeId}}">
                  <v-icon>edit</v-icon>
                </v-btn>

                <v-btn dark icon @click="deleteEmployee">
                  <v-icon>delete</v-icon>
                </v-btn>
              </v-card-title>

              <v-spacer></v-spacer>

              <v-card-title class="white--text pl-5 pt-5">
                <div class="display-1 pl-5 pt-5">{{name}}</div>
              </v-card-title>
            </v-layout>
          </v-img>

          <v-list two-line>
            <v-list-tile>
              <v-list-tile-action>
                <v-icon color="indigo">phone</v-icon>
              </v-list-tile-action>

              <v-list-tile-content>
                <v-list-tile-title>{{employeeId}}</v-list-tile-title>
                <v-list-tile-sub-title>Employee Id</v-list-tile-sub-title>
              </v-list-tile-content>

              <v-list-tile-action>
                <v-icon>chat</v-icon>
              </v-list-tile-action>
            </v-list-tile>

            <v-divider inset></v-divider>

            <v-list-tile>
              <v-list-tile-action>
                <v-icon color="indigo">work</v-icon>
              </v-list-tile-action>

              <v-list-tile-content>
                <v-list-tile-title>{{dept}}</v-list-tile-title>
                <v-list-tile-sub-title>Department</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>

            <v-divider inset></v-divider>

            <v-list-tile>
              <v-list-tile-action>
                <v-icon color="indigo">location_on</v-icon>
              </v-list-tile-action>

              <v-list-tile-content>
                <v-list-tile-title>{{position}}</v-list-tile-title>
                <v-list-tile-sub-title>Position</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  import db from "./firebaseInit";
  export default {
    name: "view-employee",
    data() {
      return {
        employeeId: null,
        name: null,
        dept: null,
        position: null
      };
    },
    beforeRouteEnter(to, from, next) {
      db.collection("employees")
        .where("employeeId", "==", to.params.employeeId)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            next(vm => {
              vm.employeeId = doc.data().employeeId;
              vm.name = doc.data().name;
              vm.dept = doc.data().dept;
              vm.position = doc.data().position;
            });
          });
        });
    },
    watch: {
      $route: "fetchData"
    },
    methods: {
      fetchData() {
        db.collection("employees")
          .where("employeeId", "==", this.$route.params.employeeId)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              this.employeeId = doc.data().employeeId;
              this.name = doc.data().name;
              this.dept = doc.data().dept;
              this.position = doc.data().position;
            });
          });
      },
      deleteEmployee() {
        if (confirm("Are you sure?")) {
          db.collection("employees")
            .where("employeeId", "==", this.$route.params.employeeId)
            .get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                doc.ref.delete();
                this.$router.push("/");
              });
            });
        }
      }
    }
  };
</script>


